.header {
    max-width: 1860px;
    margin: 50px auto 0 auto;
    display: flex;
    justify-content: space-between;
}

.header .left-side {
    display: flex;
    gap: 65px;
    align-items: center;
}

.header .left-side .logo-container {
    display: flex;
    gap: 40px;
    align-items: center;
}

.header .left-side .logo-container .burger-menu .burger-checkbox {
    position: absolute;
    visibility: hidden;
}

.header .left-side .logo-container .burger-menu .burger {
    cursor: pointer;
    display: block;
    position: relative;
    border: none;
    background: transparent;
    width: 22px;
    height: 14px;
}

.header .left-side .logo-container .burger-menu .burger::before,
.header .left-side .logo-container .burger-menu .burger::after {
    content: '';
    left: 0;
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    border-radius: 10px;
    background: #000;
}

.header .left-side .logo-container .burger-menu .burger::before {
    top: 0;
    box-shadow: 0 11px 0 #000;
    transition: box-shadow .3s .15s, top .3s .15s, transform .3s;
}

.header .left-side .logo-container .burger-menu .burger::after {
    bottom: 6.5px;
    transition: bottom .3s .15s, transform .3s;
}

.header .left-side .logo-container .burger-menu .burger-checkbox:checked + .burger::before {
    top: 2px;
    transform: rotate(45deg);
    box-shadow: 0 6px 0 rgba(0,0,0,0);
    transition: box-shadow .15s, top .3s, transform .3s .15s;
}

.header .left-side .logo-container .burger-menu .burger-checkbox:checked + .burger::after {
    bottom: 10px;
    transform: rotate(-45deg);
    transition: bottom .3s, transform .3s .15s;
}

.header .left-side .logo-container .logo {
    display: flex;
}

.header .left-side .menu {
    display: flex;
    gap: 40px;
}

.header .left-side .menu .menu-item {
    cursor: pointer;
    display: flex;
    gap: 4px;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
}

.header .left-side .menu .menu-item a {
    text-decoration: none;
    color: #000;
}

.header .left-side .menu .menu-item .arrow-hover {
    display: none;
}

.header .left-side .menu .menu-item.active,
.header .left-side .menu .menu-item:hover,
.header .left-side .menu .menu-item a:hover {
    color: #FF8E8E;
}

/* .header .left-side .menu .menu-item.active .arrow-default {
    display: none;
} */

.header .left-side .menu .menu-item.active .arrow-hover {
    display: inline-block;
}

.header .left-side .menu .menu-item:hover .arrow-hover {
    display: inline-block;

}

.header .left-side .menu .menu-item:hover .arrow-default {
    content: url(../../assets/icons/arrow-pink.svg);
}

.header .right-side {
    display: flex;
    align-items: center;
    gap: 40px;
    margin-right: 60px
}

.header .right-side .header-icon {
    display: flex;
    align-items: center;
    gap: 4px;
}

.header .right-side .header-icon a {
    display: flex;
}

.header .right-side .header-icon .counter {
    background: #000;
    color: #FFF;
    border-radius: 50%;
    padding: 2px 7px 5px 7px;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
}

.header .right-side .header-icon img:hover {
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.3), -5px -5px 30px rgba(0, 0, 0, 0.3);
}